import React from 'react';
import { Container } from '@mui/material';
import { Route, Switch } from 'react-router';
import store from '../../../app/store';
import TopBar from '../../UI/appBar/AppBar';
import { BottomNavigationComponent } from '../../UI/bottomNavigation/BottomNavigation';
import Converter from '../ConverterScreen/converter/Converter';
import { CurrencyList } from '../RatesScreen/currencyList/currencyList';
import Savings from '../SavingsScreen/savings/Savings';

const Base = () => {
  return (
    <div>
      <TopBar authStore={store.authStore} />

      <Container maxWidth="sm" sx={{ padding: '4rem 0' }} >
        <Switch>
          <Route path="/currency-MUI-pages/savings"> <Savings store={store} /> </Route>
          <Route path="/currency-MUI-pages/converter"> <Converter /> </Route>
          <Route path="/currency-MUI-pages/rates"> <CurrencyList store={store} /> </Route>
        </Switch>
      </Container>

      <BottomNavigationComponent />
    </div>
  )
}

export default Base
