import React from 'react';
import { observer } from 'mobx-react';
import {
  Switch,
  Route
} from "react-router-dom";
import { Store } from './app/features/store';
import Base from './components/Screens/Base/Base';
import LoginBase from './components/Screens/LoginAndRegisterScreen/LoginBase';
import { useMediaQuery, createTheme, ThemeProvider, CssBaseline } from '@mui/material';
import './App.css';
import firebaseConfig from './app/features/auth/firebaseConfig';
import { initializeApp } from 'firebase/app';
import { getAuth, onAuthStateChanged } from '@firebase/auth';
import { useHistory } from 'react-router';

const App: React.FC<{ store: Store }> = observer(({ store }) => {
  const history = useHistory();
  const app = initializeApp(firebaseConfig);

  const auth = getAuth();
  onAuthStateChanged(auth, (user) => {
    if (!user) {
      history.push('/login');
    } else {
      history.push('/currency-MUI-pages/rates')
    }
  })

  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)')
  const theme = React.useMemo(
    () =>
      createTheme({
        palette: {
          mode: prefersDarkMode ? 'dark' : 'light',
        },
      }),
    [prefersDarkMode],
  );

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Switch>

        <Route path="/currency-MUI-pages" component={Base} />
        <Route path="/" component={LoginBase} />

      </Switch>
    </ThemeProvider>
  );
})

export default App;


