import React, { useEffect, useState } from "react";
import { observer } from "mobx-react";
import { Store } from "../../../../app/features/store";
import { Controller, SubmitHandler, useForm } from "react-hook-form";
import { ISaving } from "../../../../app/features/store/savingsStore/savingsStore";
import { IForm } from "./Savings";
import NumberFormatCustom from "./NumberFormatCustom";
import {
   Alert,
   AlertTitle,
   Button,
   Dialog,
   DialogActions,
   DialogContent,
   DialogTitle,
   FormControl,
   IconButton,
   InputLabel,
   MenuItem,
   Select,
   SelectChangeEvent,
   Stack,
   TextField,
   Tooltip,
   Typography
} from "@mui/material";
import { DeleteOutlined } from "@material-ui/icons";

interface ISavingEdit {
   name: string,
   value: number
}

const useAddSavingsDialog = (store: Store) => {
   const formStore = store.formStore;
   const savingsStore = store.savingsStore;
   const ratesStore = store.RatesStore;

   const RATES = ratesStore.data;
   const ERROR_MSG = 'Can not be empty';
   const isOpen = formStore.isOpen;
   const isEditMode = formStore.mode === "EDIT";
   const editingItem = formStore.editingItem;
   const isOpenDeleteConfirm = formStore.isOpenDeleteConfirm;

   let formTitle = 'Add saving';
   let defaultValue = 0;
   let defaultName = '';

   if (formStore.editingItem) {
      formTitle = `Edit ${formStore.editingItem.name} saving`;
      defaultName = formStore.editingItem.name;
      defaultValue = formStore.editingItem.value;
   }

   const onClose = () => formStore.cancelEditMode();
   const openDeleteConfirm = () => formStore.openDeleteConfirm();
   const closeDeleteConfirm = (result: string) => formStore.closeDeleteConfirm(result);

   const editSaving = (data: ISaving) => savingsStore.editSaving(data);
   const addSaving = (data: ISaving) => savingsStore.addSaving(data);

   const submitForm = (data: ISaving) => {
      if (isEditMode) editSaving(data);
      else addSaving(data);
      onClose();
   };


   return {
      RATES,
      ERROR_MSG,
      isOpen,
      isEditMode,
      isOpenDeleteConfirm,
      formTitle,
      defaultValue,
      defaultName,
      editingItem,
      submitForm,
      onClose,
      openDeleteConfirm,
      closeDeleteConfirm
   }
}


const DeleteConfirmDialog: React.FC<{ isOpenDeleteConfirm: boolean, deletingItem: ISaving | null, closeDeleteConfirm: (arg0: string) => void }> = ({ isOpenDeleteConfirm, deletingItem, closeDeleteConfirm }) => {

   const deleteCancel = () => closeDeleteConfirm('NO');
   const deleteSubmit = () => closeDeleteConfirm('YES');

   return (
      <>
         <Dialog open={isOpenDeleteConfirm}>
            <DialogContent>
               <Alert
                  severity="warning"
               >
                  <AlertTitle>You're about to delete {deletingItem ? `${deletingItem.value} ${deletingItem.name}` : null}</AlertTitle>
                  <Typography variant="body1" >Are you sure you want to delete this item?</Typography>
                  <Stack direction="row" spacing={2} mt={2} justifyContent="flex-end" >
                     <Button variant="outlined" color="inherit" onClick={deleteSubmit}>Yes, I am sure</Button>
                     <Button color="inherit" onClick={deleteCancel}>Cancel</Button>
                  </Stack>
               </Alert>
            </DialogContent>
         </Dialog>
      </>
   )
}


const AddSavingsDialog: React.FC<IForm> = observer(({ store }) => {

   const {
      RATES,
      ERROR_MSG,
      isOpen,
      isEditMode,
      isOpenDeleteConfirm,
      formTitle,
      defaultValue,
      defaultName,
      editingItem,
      submitForm,
      onClose,
      openDeleteConfirm,
      closeDeleteConfirm
   } = useAddSavingsDialog(store);


   const [Name, setName] = useState(defaultName);
   const [Value, setValue] = useState(defaultValue);
   const [ValueError, setValueError] = useState('')
   const [NameError, setNameError] = useState('')

   useEffect(() => {
      setName(defaultName);
      setValue(defaultValue);
   }, [defaultName, defaultValue]);

   const validateOnChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement> | SelectChangeEvent<string>) => {
      const target = e.target;
      const value = target.value;
      if (value) {
         if (target.name === 'value') {
            setValueError('');
            setValue(Number(value));
         } else {
            setNameError('');
            setName(value);
         }
      }
      return
   }

   const handleConfirm = () => {
      if (Name.trim() && Number(Value)) {
         setNameError('');
         setName('');
         setValueError('');
         setValue(0);
         const newSaving: ISaving = {
            name: Name,
            value: Value,
            scale: 0,
            rate: 0
         }
         submitForm(newSaving)
      } else (!Name.trim()) ? setNameError(ERROR_MSG) : setValueError(ERROR_MSG)
   }

   return (
      <>
         <DeleteConfirmDialog deletingItem={editingItem} isOpenDeleteConfirm={isOpenDeleteConfirm} closeDeleteConfirm={closeDeleteConfirm} />
         <Dialog open={isOpen}>

            {RATES.length ?
               <>
                  <DialogTitle>{formTitle}</DialogTitle>
                  <DialogContent >
                     <Stack direction="row">
                        <FormControl >
                           <TextField
                              name="value"
                              error={!!ValueError}
                              helperText={ValueError}
                              margin='normal'
                              label='Amount'
                              value={Value}
                              onChange={(e) => validateOnChange(e)}
                              autoComplete='off'
                              InputProps={{
                                 inputComponent: NumberFormatCustom as any,
                              }}
                              variant='standard'
                           />
                        </FormControl>
                        <FormControl margin='normal' sx={{ width: '150px' }}>
                           <InputLabel
                              id="demo-simple-select-label"
                              error={!!NameError}
                           >
                              Currency
                           </InputLabel>
                           <Select
                              name="name"
                              labelId="currency-select-label"
                              id="currency-select"
                              error={!!NameError}
                              disabled={!!isEditMode}
                              value={Name}
                              onChange={(e) => validateOnChange(e)}
                              label="Currency"
                              variant='standard'
                           >
                              {RATES.map((rate) => (
                                 <MenuItem key={rate.Cur_ID} value={rate.Cur_Abbreviation}> {rate.Cur_Abbreviation} </MenuItem>
                              ))}
                           </Select>
                        </FormControl>
                     </Stack>
                  </DialogContent >
                  <DialogActions>
                     <Stack direction="row" width="100%" justifyContent="space-between" alignItems="center">
                        <div>
                           {isEditMode && (
                              <Tooltip title="Delete">
                                 <IconButton color="error" onClick={openDeleteConfirm}>
                                    <DeleteOutlined />
                                 </IconButton>
                              </Tooltip>)
                           }

                        </div>
                        <div>
                           <Stack direction="row" spacing={1}>
                              <Button onClick={handleConfirm} variant="contained" type="submit">Confirm</Button>
                              <Button onClick={onClose} variant="outlined" type="reset">Cansel</Button>
                           </Stack>
                        </div>
                     </Stack>

                  </DialogActions>
               </>
               :
               <>
                  <DialogContent >
                     <Typography>Please, load rates to continue.</Typography>
                  </DialogContent >
                  <DialogActions>
                     <Button onClick={onClose} type="reset">Cansel</Button>
                  </DialogActions>
               </>
            }

         </Dialog >
      </>
   );
})
export default AddSavingsDialog