import React, { useEffect, useState } from "react";
import { LineChart } from "../lineChart/LineChart";
import { Avatar, Card, CardHeader, CardMedia, Collapse, IconButton, IconButtonProps, Typography } from "@mui/material"
import { styled } from "@mui/material/styles";
import { ExpandMore } from "@material-ui/icons"
import { IRate } from "../../../../app/features/store/ratesStore/ratesStore";

export interface IChartData {
  labels: string[];
  data: number[];
}

interface ExpandMoreProps extends IconButtonProps {
  expand: boolean;
}


const ExpandChart = styled((props: ExpandMoreProps) => {
  const { expand, ...other } = props
  return <IconButton {...other} />
})(({ theme, expand }) => ({
  transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
  marginLeft: 'auto',
  transition: theme.transitions.create('transform', {
    duration: theme.transitions.duration.shortest,
  }),
}))

export const Currency: React.FC<{ rate: IRate }> = ({ rate}) => {

  const [expanded, setExpanded] = useState(false)
  const handleExpandClick = () => {
    setExpanded(!expanded)
  }


  return (
    <>
      <Card>
        <CardHeader
          avatar={
            <Avatar>{rate.Cur_Abbreviation}</Avatar>
          }
          title={`За ${rate.Cur_Scale} ${rate.Cur_Name}`}
          subheader={`Текущий курс: ${rate.Cur_OfficialRate}`}
          action={
            <ExpandChart
              expand={expanded}
              onClick={handleExpandClick}
              aria-expanded={expanded}
              aria-label="show more"
            >
              <ExpandMore />
            </ExpandChart>
          }
        />
        <Collapse in={expanded} timeout="auto" unmountOnExit>
          <CardMedia>
            {(rate.Chart_Data.labels.length && rate.Chart_Data.data.length) ?
              <LineChart
                labels={rate.Chart_Data && rate.Chart_Data.labels}
                data={rate.Chart_Data && rate.Chart_Data.data}
                label={rate.Cur_Abbreviation}
              />
              : <Typography  textAlign='center' >There is no valid data for chart.</Typography>
            }

          </CardMedia>
        </Collapse>
      </Card>
    </>
  )
}