import { makeAutoObservable } from "mobx";
import { Store } from "..";
import { IRate } from "../ratesStore/ratesStore";

export interface ISaving {
  name: string;
  value: number;
  rate: number;
  scale: number;
}

export const findRate = (data: IRate[], Cur_Abbreviation: string): number => {
  let rate = 0;
  const index = data.findIndex(
    (rate) => rate.Cur_Abbreviation === Cur_Abbreviation
  );
  if (index !== -1) {
    rate = data[index].Cur_OfficialRate;
  }
  return rate;
};

export class savingsStore {
  indexStore: Store;
  savings: ISaving[] = [];
  converted: number = 0;
  date: string;
  error: boolean = false;

  constructor(indexStore: Store) {
    makeAutoObservable(this);
    this.indexStore = indexStore;
    this.error = this.checkForRates();
    this.date = indexStore.RatesStore.today;
    this.getFromDB();
  }

  checkForRates = () => !!this.indexStore.RatesStore.data.length;

  addSaving(saving: ISaving) {
    saving.value = Number(saving.value);
    const S = this.savings.find((item) => item.name === saving.name);
    if (S) {
      S.value += saving.value;
    } else this.savings.push(saving);
    this.saveToDB();
  }

  editSaving(savingEdit: ISaving) {
    savingEdit.value = Number(savingEdit.value);
    const S = this.savings.find((item) => item.name === savingEdit.name);
    if (S) {
      S.value = savingEdit.value;
    }
      this.saveToDB();
  }

  removeSaving(saving: ISaving) {
    const index = this.savings.findIndex((item) => item.name === saving.name);
    this.savings.splice(index, 1);
    this.saveToDB();
    return this.savings;
  }

  saveToDB() {
    this.convertToBYN();
    localStorage.setItem("savings", JSON.stringify(this.savings));
  }

  getFromDB() {
    const savingsAtDB = localStorage.getItem("savings");
    if (savingsAtDB) {
      const S: ISaving[] = JSON.parse(savingsAtDB);
      S.forEach((save) => this.addSaving(save));
    }
    this.convertToBYN();
  }

  convertToBYN() {
    let result: number = 0;
    const rates = this.indexStore.RatesStore.data;
    this.savings.forEach((elem) => {
      const index = rates.findIndex(
        (rate) => rate.Cur_Abbreviation === elem.name
      );
      if (index !== -1) {
        const rate = rates[index];
        elem.rate = rate.Cur_OfficialRate;
        elem.scale = rate.Cur_Scale;
        result += (elem.value * elem.rate) / elem.scale;
      }
    });
    this.converted = result;
  }
}
