import React from "react";
import store from "../../../../app/store";
import { ISaving } from "../../../../app/features/store/savingsStore/savingsStore";

import Converted from "./Converted";
import { Chip, Stack } from "@mui/material";
import { Box } from "@mui/system";
import { observer } from "mobx-react";
import { Store } from "../../../../app/features/store";

const useSavingsList = (store: Store) => {
   const formStore = store.formStore;
   const savingsStore = store.savingsStore;

   const handleSavingEditClick = (saving: ISaving) => {
      formStore.setEditMode(saving);
   }

   const savings = savingsStore.savings;
   return { savings, handleSavingEditClick}
}


const SavingsList: React.FC<{store: Store}> = observer(({store}) => {

   const { savings, handleSavingEditClick } = useSavingsList(store);

   return (<>
      <Box sx={{ padding: '1.5rem 0' }}>
         <Stack direction="row" spacing={2} justifyContent="space-evenly" sx={{ flexWrap: "wrap", paddingBottom: '1rem' }}>
            {savings.length ? savings.map(saving => <Chip
               key={saving.name}
               variant="outlined"
               color="primary"
               label={`${saving.value} ${saving.name}`}
               onClick={() => handleSavingEditClick(saving)}
            />) : null}
         </Stack>
         <Converted store={ store}/>
      </Box>
   </>)
}) 

export default SavingsList