import React from 'react';
import { useHistory } from 'react-router';
import { Button, FormControl, Link, Stack, TextField, Typography } from '@mui/material';
import { Controller, useForm } from 'react-hook-form';
import { getAuth, signInWithEmailAndPassword } from 'firebase/auth';

interface IFormLogin {
  login: string,
  password: string,
}

const useLoginForm = () => {

  const history = useHistory();
  
  const auth = getAuth();


  const goTo = (url: string) => {
    history.push(url);
  }


  const handleLogin = (data: IFormLogin) => {
    signInWithEmailAndPassword(auth, data.login, data.password)
      .then((userCred) => {
        const user = userCred.user;
        goTo('/currency-MUI-pages/rates');
      })
      .catch(error => {
        const errorCode = error.code;
        const errorMessage = error.message;
      })
  }

  return { goTo, handleLogin }
}

const LoginForm = () => {

  const { goTo, handleLogin } = useLoginForm();

  const { reset, formState: { errors }, handleSubmit, control } = useForm<IFormLogin>({
    defaultValues: {
      login: '',
      password: '',
    },
    mode: "onChange"
  })

  return (
    <div>
      <Typography variant="h4" >
        Login
      </Typography>
      <Stack mt={3} mb={3} direction="column" spacing={2}>

        <FormControl>
          <Controller
            name="login"
            control={control}
            rules={{
              required: { value: true, message: 'Can not be empty' },
              minLength: { value: 5, message: 'Can not be shorter 5 symbols' },
            }}
            render={({ field }) => <TextField
              {...field}
              error={!!errors.login}
              helperText={errors.login ? errors.login.message : null}
              type="email"
              id="login"
              label="Login"
              variant="filled"
            />}
          />
        </FormControl>

        <FormControl>
          <Controller
            name="password"
            control={control}
            rules={{
              required: { value: true, message: 'Can not be empty' },
              minLength: { value: 5, message: 'Can not be shorter 5 symbols' },
              maxLength: { value: 14, message: 'Can not be longer 14 symbols' },
            }}
            render={({ field }) => <TextField
              {...field}
              error={!!errors.password}
              helperText={errors.password ? errors.password.message : null}
              type="password"
              id="password"
              label="Password"
              variant="filled"
            />}
          />
        </FormControl>

      </Stack>

      <Stack mt={2} direction="column" spacing={2}>
        <Button variant="contained" onClick={handleSubmit(handleLogin)} >Log in</Button>
        <Typography>
          Don't have and account?

          <Link sx={{ cursor: 'pointer' }}
            onClick={() => goTo('/register')} >
            Register
          </Link>

        </Typography>
      </Stack>

    </div>
  )
}

export default LoginForm
