import React, { useState } from "react";
import { observer } from "mobx-react";
import { UseFormReset } from "react-hook-form";
import { Store } from "../../../../app/features/store";
import { ISaving } from "../../../../app/features/store/savingsStore/savingsStore";

import AddSavingsDialog from "./FormAddSavings";
import SavingsList from "./SavingsList";
import { Fab } from "@mui/material";

import { Add } from "@material-ui/icons";


export interface IForm {
   store: Store;
}

const Savings: React.FC<{ store: Store }> = observer(({ store }) => {

   const Savings = store.savingsStore.savings;
   const [open, setOpen] = useState(false);

   const handleClickModal = () => {
      store.formStore.toggleForm();
   }

   const handleClose = (resetFunc: UseFormReset<ISaving>) => {
      setOpen(false);
      resetFunc();
   }

   return (
      <>
         <SavingsList store={store} />
         <AddSavingsDialog store={store} />
         <Fab
            sx={{ margin: "auto 10px 10px auto", position: "fixed", right: '1.5rem', bottom: '5rem' }}
            color="primary"
            aria-label="add"
            onClick={handleClickModal}
         >
            <Add />
         </Fab>
      </>
   );
})

export default Savings;
