import React, { useEffect } from 'react';
import { observer } from 'mobx-react';
import { Store } from '../../../../app/features/store';
import { IRate } from '../../../../app/features/store/ratesStore/ratesStore';

import { Currency } from '../currency/Currency';

import { CircularProgress, Stack } from '@mui/material';
import { Box } from '@mui/system';


export const CurrencyList: React.FC<{ store: Store }> = observer(({ store }) => {

    const Currencies = store.RatesStore.data;

    useEffect(() => {
        if (!Currencies.length) {
            store.initRatesStore();
        }
    }, [store, Currencies]);

    return (
        <Box sx={{ padding: '1.5vh 3%' }}>
            <Stack direction='column' spacing={2} >
                {Currencies.length ?
                    Currencies.map((rate: IRate) => <Currency
                        key={rate.Cur_ID}
                        rate={rate}
                    />)
                    : <Box sx={{ display: 'flex', justifyContent: 'center' }}> <CircularProgress /> </Box>}
            </Stack>
        </Box>
    )
})