import React, { useEffect, useState } from 'react';
import { Typography } from '@mui/material';
import { observer } from 'mobx-react';
import { Store } from '../../../../app/features/store';

const Converted: React.FC<{ store: Store }> = observer(({ store }) => {
  const converted = store.savingsStore.converted;
  const [convertedError, setconvertedError] = useState('');

  useEffect(() => {
    if (!store.RatesStore.data.length) {
      setconvertedError("You probably need to load rates first");
    } else {
      store.savingsStore.convertToBYN();
    }
  }, [])

  return (
    <Typography variant="h4" textAlign="center">{convertedError ? convertedError : converted.toFixed(2)}</Typography>
  )
})

export default Converted;
