import { makeAutoObservable } from "mobx";
import { Store } from "..";
import { IChartData } from "../../../../components/Screens/RatesScreen/currency/Currency";

const CURRENCY_ID_LIST = [
  451, //? Евро
  431, //? Доллар США
  456, //? Российский рубль
  468, //? Тайский бат
  460, //? Турецкая лира
  426, //? Швейцарский франк
  429, //? Фунт стерлингов
  449, //? Гривна
  // 'https://blockchain.info/ticker',                 //? Bitcoin
];

const sendRequest = async (url: string) => {
  const response = await fetch(url);
  return await response.json();
};

const getDates = (spread: number) => {
  const today = new Date();
  const firstStatDay = new Date();
  firstStatDay.setDate(today.getDate() - spread);
  return [
    `${firstStatDay.toJSON().slice(0, 10)}`,
    `${today.toJSON().slice(0, 10)}`,
  ];
};

export interface IStatistics {
  Cur_ID: number;
  Date: string;
  Cur_OfficialRate: number;
}

export interface IRate {
  Cur_ID: number;
  Date: string;
  Cur_Abbreviation: string;
  Cur_Scale: number;
  Cur_Name: string;
  Cur_OfficialRate: number;
  Cur_Statistics: IStatistics[];
  Chart_Data: IChartData;
}

export class RatesStore {
  indexStore: Store;
  data: IRate[] = [];
  startDate: string;
  today: string;

  constructor(indexStore: Store) {
    makeAutoObservable(this);
    const dates = getDates(40);
    this.startDate = dates[0];
    this.today = dates[1];
    this.indexStore = indexStore;
  }

  fetchData() {
    CURRENCY_ID_LIST.forEach(async (id: number) => {
      const rate: IRate = await sendRequest(
        `https://www.nbrb.by/api/exrates/rates/${id}`
      );
      this.putRate(rate);
    });
  }

  async putRate(rate: IRate) {
    const statAndChart = await this.getRateStatistics(rate.Cur_ID);
    rate.Cur_Statistics = statAndChart.Cur_Statistics;
    rate.Chart_Data = statAndChart.Chart_Data;
    this.data.push(rate);
  }

  async getRateStatistics(id: number) {
    const Cur_Statistics: IStatistics[] = await sendRequest(
      `https://www.nbrb.by/API/ExRates/Rates/Dynamics/${id}?startDate=${this.startDate}&endDate=${this.today}`
    );
    
    const Chart_Data = this.convertStatToChart(Cur_Statistics);
    return { Cur_Statistics, Chart_Data };
  }

  convertStatToChart(stat: IStatistics[]): IChartData {
    const chartData: IChartData = {
      labels: [],
      data: [],
    };
    stat.map(day => {
      day.Date = day.Date.slice(5, 10)
      chartData.labels.push(day.Date);
      chartData.data.push(day.Cur_OfficialRate);
    })
    return chartData;
  }

}
