import { makeAutoObservable } from "mobx";
import { Store } from ".";
import { ISaving } from "./savingsStore/savingsStore";
const ADD = "ADD";
const EDIT = "EDIT";
const MODE = ["ADD", "EDIT"];
const RESULT = ["NO", "YES"];

export class FormStore {
  indexStore;
  mode: typeof ADD | typeof EDIT = ADD;
  editingItem: ISaving | null = null;
  isOpen: boolean = false;
  isOpenDeleteConfirm: boolean = false;

  constructor(indexStore: Store) {
    makeAutoObservable(this);
    this.indexStore = indexStore;
  }

  toggleForm() {
    this.isOpen = !this.isOpen;
  }

  openDeleteConfirm() {
    this.isOpenDeleteConfirm = true;
  }

  closeDeleteConfirm(result: typeof RESULT[0] | typeof RESULT[1]) {
    this.isOpenDeleteConfirm = false;
    if (result === RESULT[1] && this.editingItem) {
      this.indexStore.savingsStore.removeSaving(this.editingItem);
      this.cancelEditMode();
    }
  }

  setEditMode(saving: ISaving) {
    this.mode = EDIT;
    this.editingItem = saving;
    this.isOpen = true;
  }

  cancelEditMode() {
    this.isOpen = false;
    this.editingItem = null;
    this.mode = ADD;
  }
}
