import { Button, Link, Stack, TextField, Typography } from '@mui/material'
import React from 'react'
import { useHistory } from 'react-router-dom'

const RegisterForm = () => {
  const history = useHistory();
  const goToLoginPage = () => {
    history.push('/login');
  }
  return (
    <div>
      <Typography variant="h4" >
        Register
      </Typography>
      <Stack mt={3} mb={3} direction="column" spacing={2}>
        <TextField id="login" label="Login" variant="filled" />
        <TextField type="password" id="password" label="Password" variant="filled" />
        <TextField type="password" id="password-repeat" label="Repeat password" variant="filled" />
      </Stack>

      <Stack mt={2} direction="column" spacing={2}>
        <Button variant="contained" >Register</Button>
        <Typography>Already registered? <Link sx={{ cursor: 'pointer' }} onClick={goToLoginPage} >Sign in</Link> </Typography>
      </Stack>
    </div>
  )
}

export default RegisterForm
